<template>
  <div>
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span>{{ contentDetail.title }}</span>
      </div>
      <p>{{ contentDetail.content }}</p>
    </el-card>
  </div>
</template>

<script>
import { getApiBaseUrl } from '@/utils/api'

export default {
  name: 'Content',
  mounted () {
    this.pullContent()
  },
  data () {
    return {
      contentDetail: {
        title: '',
        content: ''
      }
    }
  },
  methods: {
    pullContent () {
      this.$http.get(getApiBaseUrl() + '/content_detail.php', {params: {'id': this.$route.params['id']}}).then(response => {
        this.contentDetail = response.body
      }, response => {
      })
    }
  }
}
</script>

<style scoped>
</style>



