<template>
  <div>
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span>热点列表</span>
      </div>
      <div v-for="(item, index) in contentList" :key="'item-' + index" class="text item" @click="toContent(item.id)" style="color: #66ccff">
        {{ item.title }}
      </div>
    </el-card>
  </div>
</template>

<script>
import { getApiBaseUrl } from '@/utils/api'

export default {
  name: 'Index',
  mounted () {
    this.pullContentList()
  },
  data () {
    return {
      contentList: []
    }
  },
  methods: {
    toContent (id) {
      this.$router.push({ name: 'Content', params: { id: id } })
    },
    pullContentList () {
      this.$http.get(getApiBaseUrl() + '/content_list.php').then(response => {
        this.contentList = response.body
      }, response => {
      })
    }
  }
}
</script>

<style scoped>
</style>



