<template>
  <div id="app">
    <el-container>
      <el-header
        height="45px"
        :style="{ 'background-color': primaryColor, paddingTop: '10px'}"
      >
        <el-row type="flex" class="row-bg" justify="space-around">
          <el-col :span="3"><el-button size="mini" round @click="$router.push({'name': 'Index'})">热点</el-button></el-col>
          <el-col :span="3" class="value">BUU 新闻网</el-col>
          <el-col :span="3"><el-button size="mini" round @click="$router.push({'name': 'Login'})">登录</el-button></el-col>
        </el-row>
      </el-header>
      <el-main>
        <router-view/>
      </el-main>
    </el-container>
  </div>
</template>

<script>
export default {
  name: 'App',
  data: function () {
    return {
      primaryColor: '#409eff'
    }
  }
}
</script>

<style>
  .value {
    color: #FFFFFF;
  }

  #app {
    font-family: Helvetica, sans-serif;
  }
</style>



